.navbar {
    background-color: #7393B3;
    width: 100%;
    height: 88px;
    display: flex;
}

.navbar__left {
    flex: 50%;
    display: flex;
    justify-content: left;
    align-items: center;
}

.navbar__left #hidden {
    display: flex;
}

.navbar__left > button {
    display: none;
    margin-left: 20px;
}

.navbar__left-links {
    max-height: 80px;
}

.navbar__left-links > a {
    text-decoration: none;
    color: white;
    font-size: 20px;
    margin-left: 15px;
}

.navbar__left-links > a:hover {
    color: blanchedalmond;
}


.navbar__social {
    flex: 50%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 50px;
    background-color: none;
}

.navbar__social > button {
}


@media only screen and (max-width: 850px) {
    .navbar__left-links a {
        font-size: 18px;
    }
}

@media only screen and (max-width: 750px) {
    .navbar__left-links a {
        font-size: 15px;
    }
}

@media only screen and (max-width: 650px) {
    .navbar__left > button {
        display: flex;
    }

    .navbar__left-links {
        display: none;
    }

    .navbar__left #hidden {
        position: absolute;
        left: 0px;
        top: 80px;
        height: 230px;
        max-height: 230px;
        width: 100%;
        background-color: #7393B3;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .navbar__left #hidden a {
        margin: 10px;
        font-size: 25px;
    }
}